.App {
  font-family: 'Abel', sans-serif;
  text-align: center;
  font-size: 1.5em;
  box-sizing: border-box;
  overflow: auto;
  height: 100vh;
  position: relative;
  background-image: url(./assets/clique.png);
  background-size: cover;
  background-position: 1% 25%;
  
}


.row {
  display: flex;
  justify-content: space-evenly;
}

.col {
  flex: 1;
display: flex;
flex-direction: column;
align-items: center;
}

h3 {
  font-variant: small-caps;
}

.comment {
  color: #313131;
}

.clique {
  width: 800px;
  height: 450px;
}

.videocontainer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: -99;
}

